import {getRegistLogin} from "common/network/meInfo.js"
export default{
	data() {
		return {
			globalUser: {}
		}
	},
	onShow() {
		var globalUser = this.getGlobalUser("globalUser");
		this.globalUser = globalUser;
	},
	methods: {
		modifySex() {
			uni.navigateTo({
				url: "../sex/sex"
			})
		},
		modifyBirthday() {
			uni.navigateTo({
				url: "../meBirthday/meBirthday"
			})
		},
		modifyNickname() {
			uni.navigateTo({
				url: "../meNickname/meNickname"
			})
		},
		operator() {
			var me = this;
			var globalUser = me.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册选择上传"],
				success(res) {
					var index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success(res) {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];






								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath,
								})

							}
						})
						
					}
				}
			})
		},
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: '清除缓存成功',
				mask: false,
				duration: 1500
			})
		},
		logout() {
			var globalUser = this.getGlobalUser("globalUser");
			getRegistLogin(globalUser).then(data => {
				var [error, res] = data;
				if(res.data.status == 200) {
					uni.removeStorageSync("globalUser");
					uni.showToast({
						title: '退出登录成功',
						mask: false,
						duration: 1000
					})
					uni.switchTab({
						url: "../me/me"
					});
				}
			})
		}
	}
}
